    rgba(46,42,94, 1) 100%
  );
}

.projectOverlay {
  width: 100%;
  height: 100%;
  z-index: 15;
  position: absolute;
  background: linear-gradient(
    0deg,
    rgba(46,42,94, 1) 0%,
    rgba(46,42,94, 0.2) 20%,
    rgba(255,255,255, 0.1) 50%,
    rgba(46,42,94, 0.2) 80%,
    rgba(46,42,94, 1) 100%
  );
}

.videoSectionLeft {
  width: 70%;
  height: 100%;
  position: relative;
}

.videoSectionRight {
  width: 30%;
