/* token module (tesla style stat head-up) */
.tokenModule {
  display: none;
}


.token h2 {

  text-align: center;
  margin-bottom: 5px;
  transform: scale(1.2);
  font-weight: 900;
  color: 'rgb(240, 248, 255)'
}

.token h6 {

  text-align: center;
  transform: scale(1.2);
  font-weight: 100;
  color: 'rgb(240, 248, 255)'
}

.token {
  max-width: 150px;
  padding: 0 30px;
}
.token h6:hover, .token h2:hover{
  color: rgb(240, 248, 255);
  text-shadow: 0px 0px 8px rgba(255,255,255,0.6);
}
.secondtoken {
  border-right: 1px solid #0e0e0e;
  border-left: 1px solid #0e0e0e;
}

@media only screen and (max-width: 450px) {
  .tokenModule {
    bottom: 0px;
  }

  .token {
    max-width: 100px;
    padding: 0 15px;
  }
}

/* Play Button componet */

.playButtonContainer {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.playButton {
  width: 100px;
  height: 100px;
  border-radius: 300px;
  border: 1px solid rgb(240, 248, 255);
  display: flex;
  align-items: center;
  justify-content: center;

  -webkit-user-select: none;

      -ms-user-select: none;

          user-select: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

/* Simple  Button component */

.simpleBtn {
  border-radius: 60px;
border: 2px solid rgb(240, 248, 255);
  transition: all 0.6s ease;
  cursor: pointer;
  text-align: center;
  background-color: transparent;
  color: rgb(240, 248, 255);
  margin: 10px 0px 10px 0;
}

.simpleBtn h4 {
  padding: 10px 20px;
  text-align: center;
  white-space: nowrap;
}

.simpleDarkBtn {
border: 2px solid rgb(240, 248, 255);
  color: rgb(240, 248, 255);
}
.simpleBtnDark {
border: 2px solid rgb(240, 248, 255);
  color: rgb(240, 248, 255);
  font-weight: 700;
}

.simpleLightBtn {
border: 2px solid rgb(240, 248, 255);
  color: rgb(240, 248, 255);
}

.simpleBtn:hover {
  background-color: rgba(256, 256, 256,1);
  color: #113355;
  border-color: rgb(240, 248, 255);
}

.simpleBtnDark:hover {
  background-color: rgba(256, 256, 256,1);
  color: #113355;
  border-color: rgb(240, 248, 255);

}
.simpleBtnDark h4:hover {
  color: #113355;

}
