/* Big Title */

.bigTitle {
  width: 100%;
  margin-bottom: 50px;
  text-align: center;
}

@media only screen and (max-width: 900px) {
  .bigTitle {
    width: auto;
  }
}
@media only screen and (max-width: 900px) {
  .siteSubtitle {
    display: none;
  }
}
/* Video Section Component component */

.videoSection {
  z-index: 20;
  width: 100%;
  height: 100vh;
  min-height: 600px;
  position: relative;
  overflow: hidden;
  background-color: rgba(206, 208, 237,0);
  display: flex;
  flex-direction: row;
}

.videoSection video {
  /* position: absolute;
  z-index: 0;
  margin-left: 50vw;
